<template lang="pug">
	transition(name="fade" mode="out-in")
		.good(v-if="display")
			.good__image(v-if="good.preview")
				img.good__image-img(:src="good.preview" alt="Image not available")
			.good__info
				span.good__title {{ good.name }}
				span.good__manufacturer manufacturer
				strong.good__price ${{ good.price }}
				template(v-if="good.amount")
					strong.good__price-sep /
					span.good__amount {{ good.amount }}
				span.good__desc {{ good.description }}
				span.good__in-cart(v-if="amountInCart")
					span.good__in-cart-text in cart 
					strong.good__in-cart-amount {{ amountInCart }}
					button.good__in-cart-remove(@click="cartButtonHandler(good, false)") -
			button.good__add(@click="cartButtonHandler(good)") +
			
</template>

<script>
	export default {
		name: 'CGood',
		props: {
			store: Object,
			good: Object,
			added: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				display: false
			}
		},
		mounted() {
			this.display = true
		},
		computed: {
			amountInCart() {
				const added = this.added;

				const cart = this.$store.getters.cart;
				const storeId = 's' + this.good.store;

				if(storeId in cart) {
					const goodId = 'g' + this.good.id;

					if(goodId in cart[storeId].goods) {
						return cart[storeId].goods[goodId].added;
					}
				}

				return added;
			}
		},
		methods: {
			cartButtonHandler(good, actionAdd = true) {
				let store = this.$store.getters.cart['s' + good.store];

				if(store) {
					let g = store.goods['g' + good.id];

					if(g) {
						const added = actionAdd
							? ++g.added
							: --g.added;
					} else {
						g = {
							id: good.id,
							store: store.id,
							name: good.name,
							price: good.price,
							amount: good.amount,
							preview: good.preview,
							added: 1
						}
					}



					store.goods = {
						...store.goods,
						...{['g' + good.id]: g}
					}

					if(g.added <= 0) {
						delete store.goods['g' + good.id];
					}
				} else {
					const s = this.store;

					store = {
						id: s.id,
						name: s.name,
						preview: s.preview,
						goods: {
							['g' + good.id]: {
								id: good.id,
								store: s.id,
								name: good.name,
								price: good.price,
								amount: good.amount,
								preview: good.preview,
								added: 1
							}
						}
					}
				}

				// if store without good alreaty remove it

				this.$store.commit('cartUpdate', {['s' + store.id]: store});
			}
		}
	}
</script>

