<template lang="pug">
	transition(name="fade" mode="out-in")
		.store(v-if="display" :ref="'store' + store.id" :class="{store_open: current && !target}")
			.store__wrap
				.store__main
					router-link.store__link(v-if="!single || target" @click.native.prevent="openStore(store.id)" event="" to="/")
						span.store__name {{ store.name }}
						span.store__trust
							span.store__verify ✓
							span.store__rating 4.0
					h1.store__link(v-else @click="openStore(store.id)")
						span.store__name {{ store.name }}
						span.store__trust
							span.store__verify ✓
							span.store__rating 4.0

					.store__preview(@click="previewClickHandler" :class="{store__preview_big: previewOpen && current}")
						img.store__preview-img(:src="store.preview" :class="{'store__preview-img_zindex': previewOpen && current}")
					span.store__address {{ store.address }}
				.store__meta
					span.store__worktime {{ store.worktime }}
					span.store__payment {{ store.payment }}
			iframe.store__mapframe(v-if="current && !target" :src="store.maplink" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0")
			.store__goods(v-if="current && !target")
				router-link.store__goods-cart(to="/cart") Cart ({{ goodsAmount }})
				//- 	span(v-text="Object.keys(store.goods).length")
				button.store__button-close(v-if="!single" @click="closeHandler") Exit
				.store__title(v-if="goods") Goods
				CGood(v-for="good in goods" :store="store" :good="good" :added="good.added")
</template>

<script>
import CGood from './CGood'
export default {
	name: 'CStore',
	props: {
		store: Object,
		single: {
			type: Boolean,
			default: false
		},
		target: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			display: false,
			goods: null,
			previewOpen: false
		}
	},
	components: {
		CGood
	},
	mounted() {
		if(this.single) {
			this.openStore(this.store.id);
		}

		if(this.store.id) {
			if(this.current) {
				if(!this.target) {
					this.getGoods();
				}

				setTimeout(() => {
					window.scrollTo({
					  top: this.$refs['store' + this.store.id].offsetTop,
					  behavior: 'smooth',
					})
				}, 100)
			}

		}
		this.display = true
	},
	computed: {
		current() {
			return this.store.id === this.$store.getters.currentStore;
		},
		goodsAmount() {
			let amount = 0;
			const cart = this.$store.getters.cart;
			const store = 's' + this.$store.getters.currentStore;

			if(store in cart) {
				for(const goodId in cart[store].goods) {
					amount += cart[store].goods[goodId].added;
				}
			}

			return amount;
		},
		worktime() {
			return store.worktime
		}
	},
	methods: {
		previewClickHandler() {
			if(this.current) {
				this.previewOpen = !this.previewOpen;
			}
		},
		openStore(id) {
			if((this.current || this.target) && id) {
				this.$router.push('/' + id)
					.catch(() => {
						// console.log(err)
					});
				return;
			}

			this.previewOpen = false;
			this.$store.commit('setCurrentStore', id);
			this.getGoods();

			setTimeout(() => {
				window.scrollTo({
				  top: this.$refs['store' + id].offsetTop,
				  behavior: 'smooth',
				})
			}, 100)
		},
		getGoods() {
			// need do it on store and save on store
			// like this.$store.mutation(...)
			const storeId = this.$store.getters.currentStore;
			if(storeId) {
				this.$req('goods?store=' + storeId, 'GET')
					.then(res => {
						this.goods = res.goods
					})
			}
		},
		closeHandler() {
			const id = this.$store.getters.currentStore;
			this.$store.commit('setCurrentStore', null);

			setTimeout(() => {
				window.scrollTo({
				  top: this.$refs['store' + id].offsetTop,
				  behavior: 'smooth',
				})
			}, 100)
		}
	}
}
</script>

